    </div>
    <div class="notice-area box-class" v-if="btnDialogVisible&&stepNumber===2">
      <el-form label-position="left" :model="btnData" label-width="180px" size="small" class="text-over">
        <span class="title-class">按钮</span>
        <el-form-item label="按钮名称：">
