        </el-form-item>
        <el-form-item label="应用界面类名：" v-if="btnData.appPackageName">
          {{btnData.appInterfaceClassName}}
        </el-form-item>
        <el-form-item label="url：" v-if="btnData.appPackageName">
          {{btnData.url}}
        </el-form-item>
        <el-form-item label="快应用链接：">
          {{btnData.cpFastLink}}
        </el-form-item>
        <el-form-item label="h5链接：" prop="cpH5Link">
          {{btnData.cpH5Link}}
        </el-form-item>
      </el-form>
    </div>
    <div class="notice-area box-class" v-if="noticeDialogVisible&&stepNumber===2">
      <span class="title-class">通知</span>
      <el-form label-position="left" :model="noticeData" label-width="180px" size="small" class="text-over">
        <el-form-item label="通知标题：" class="formitem-class">
          {{noticeData.title}}
        </el-form-item>
        <el-form-item label="通知内容：" class="formitem-class">
          {{noticeData.content}}
        </el-form-item>
        <el-form-item label="出现时间：" class="formitem-class">
          <div class="radio-input">
            <div v-if="noticeData.startType===0">跟随tips出现</div>
          </div>
        </el-form-item>
        <el-form-item label="是否允许通知在21：00~次日7：00出现：" class="formitem-class" label-width='300px'>
          {{noticeData.isAllowNotice===1?'允许':'不允许'}}
        </el-form-item>
        <el-form-item label="消失时间：" prop="disappearTime" class="formitem-class">
          <div class="radio-input">
            <div v-if="noticeData.disappearType===0">跟随tips结束</div>
            <div v-if="noticeData.disappearType===1">通知出现</div>
            <div v-if="noticeData.disappearType===1">{{noticeData.disappearTime}}</div>
            <div v-if="noticeData.disappearType===1">分钟后</div>
          </div>
        </el-form-item>
      </el-form>
    </div>

    <!-- 审核进度 -->
    <div class="notice-area box-class">
      <span class="title-class">审核进度</span>
      <div class="check-div" v-if="approvalData.approvalTestStatus===0"><i class="el-icon-remove" style="color: #909399"></i>测试人员未审核</div>
      <div class="check-div" v-if="approvalData.approvalTestStatus===1"><i class="el-icon-success" style = "color: #409EFF"></i>测试人员审核：测试工程师{{approvalData.approvalTestUserName}}审核通过</div>
      <div class="check-div" v-if="approvalData.approvalTestStatus===2"><i class="el-icon-error" style="color: #f56c6c;"></i>测试人员审核：测试工程师{{approvalData.approvalTestUserName}}审核不通过原因为：{{approvalData.approvalTestReason}}</div>

      <div class="check-div" v-if="approvalData.approvalOperationStatus===0"><i class="el-icon-remove" style="color: #909399"></i>运营人员未审核</div>
      <div class="check-div" v-if="approvalData.approvalOperationStatus===1"><i class="el-icon-success" style = "color: #409EFF"></i>运营人员审核：运营经理{{approvalData.approvalOperationUserName}}审核通过</div>
      <div class="check-div" v-if="approvalData.approvalOperationStatus===2"><i class="el-icon-error" style="color: #f56c6c;"></i>运营人员审核：运营经理{{approvalData.approvalOperationUserName}}审核不通过原因为：{{approvalData.approvalOperationReason}}</div>
    </div>

    <div class="buttons-area box-class" v-if="isCheckpage">
      <el-button @click="cancelEditButton()" type="primary" round class="button-class">返回</el-button>
      <el-button @click="previewTest()" type="primary" round class="button-class">预览测试</el-button>
      <el-button @click="passButton(true)" type="primary" round class="button2-class" :disabled="isExam">通过</el-button>
      <el-button @click="passButton(false)" type="primary" round class="button-class" :disabled="isExam">不通过</el-button>
    </div>
    <el-dialog title="输入测试IMEI" :visible.sync="testDialogVisible" width="600px" center>
      <div class="test-dialog">
        <span>输入1-20个IMEI码</span>
        <div style="display: flex;margin:20px">
        <el-input v-model="imeiTestValue" placeholder="请输入IMEI" style="width:200px;margin-right: 20px;"></el-input>
        <el-button type="primary" size="mini" @click="saveIMEI()">添加</el-button>
        </div>
        <div style="display:flex;width:100%;padding:10px 20px;flex-wrap: wrap">
          <el-tag v-for="(imei, index) in imeiList" :key="index" closable type="info" @close="deleteIMEI(index)" style="margin-right: 10px;margin-bottom: 10px">
            {{imei}}
          </el-tag>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="testDialogVisible=false">取 消</el-button>
        <el-button type="primary" @click="testBtn()">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="审核说明" :visible.sync="examDialogVisible" width="600px" :before-close="examDialogClose" center>
      <el-form :model="baseData" ref="examData" label-width="100px">
        <el-form-item label=" 审核说明" prop="reason" :rules="applyRules">
          <el-input v-model="baseData.reason" type="textarea" :autosize="{ minRows: 2, maxRows: 3}" placeholder="请填写审核说明" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{baseData.reason?baseData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="examDialogClose(true)">取 消</el-button>
        <el-button type="primary" @click="examDialogClose()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import global from "../service/global";

export default {
  name: "tips-component",
  props: {
    isCheckpage: {
      type: Boolean,
      default: true
    },
    isDetailpage: {
      type: Boolean,
      default: false
    },
    baseData: {}
  },
  activated() {
  },
  data() {
    var specialValidate = (rule, value, callback) => {
      if (/^\s+$/.test(value)) {
        callback(new Error("不能输入全空格"));
      } else {
        callback();
      }
    };
    return {
      isCeshi: true,//调试用
      grayscaleDataTypes: [],
      excelFileName: "",
      excelFile: "",
      uploadLoading: false,
      controlType: 1,
      grayscaleType: 1,
      downloadFileName: '',
      downloadFileUrl: '',
      isMantissaList1: false,
      isMantissaList2: false,
      imeiList: [],
      imeiValue: "",
      imeiData: {
        grayscaleDataType: '',
        releaseType: ''
      },
      mantissaList1: [],
      mantissaList2: [],
      isNoticeTimeError: false,
      isTimeError1: false,
      isTimeError2: false,
      startTimeDisabled: false,
      testCardUrl: "",
      isExam: false,
      imeiTestValue: "",
      xiajiaDialogVisible: false,
      jinyongDialogVisible: false,
      isEditPage: true,
      examDialogVisible: false,
      testImgSrc: "",
      testDialogVisible: false,
      xiajiaReason: "",
      jinyongReason: "",
      bohuiReason: "",
      approvalData: {

      },
      noticeData: {
        title: "",
        content: "",
        startType: "",
        endType: "",
        endTime: "",
        timeSwitch: false
      },
      btnData: {
        linkType: 2
      },
      noticeDialogVisible: false,
      btnDialogVisible: false,
      contentData: {
        icon: '',
        time: [],
        priority: '',
        content: ''
      },
      textLinkData: {

      },
      nameData: {
        name: ''
      },
      cardPreview: "",
      expandPreview: "",
      holidayList: global.holidayList,
      stepNumber: 1,
      basicData: {
        peopleLabels: [],
        bindType: 2
      },
      timeData: {
        type: 0,
        startTime: "",
        endTime: "",
        holiday: ""
      },
      applyRules: [
        { required: true, message: "内容不能为空", trigger: "blur" },
        { required: true, validator: specialValidate, trigger: "blur" }
      ]
    };
  },
  methods: {
    saveIMEI(){
      if (this.imeiList.length > 19) {
        this.$confirm("只能输入1-20个IMEI", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
      } else if (this.imeiList.includes(this.imeiTestValue)) {
        this.$confirm("IMEI码重复！", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
      } else if (/^\d{15}$/.test(this.imeiTestValue)) {
        this.imeiList.push(this.imeiTestValue);
      } else {
        this.$confirm("IMEI码不符合要求！(15位数字)", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
      }
    },
    deleteIMEI(index) {
      this.imeiList.splice(index, 1);
    },
    testBtn() {
      this.uploadLoading = true;
      let param = {
        tipsId: this.baseData.tipsId,
        status: this.baseData.status,
        versionCode: this.baseData.versionCode,
        name: this.nameData.name,
        startTime: this.contentData.time[0],
        endTime: this.contentData.time[1],
        priority: this.contentData.priority,
        iconUrl: this.contentData.iconUrl,
        displayText: this.contentData.displayText,
        textLink: this.textLinkData,
        buttonName: this.btnDialogVisible?this.btnData.buttonName:'',
        buttonLink: this.btnDialogVisible?this.btnData:undefined,
        notice: this.noticeDialogVisible?this.noticeData:undefined,
        imeis: this.imeiList.join(';')
      };
      this.$http
        .post("/scene/previewTips", param)
        .then(response => {
          if(response.data.code===0){
            this.$message({
                  type: "success",
                  message: "预览推送成功!"
                });
            this.uploadLoading = false;
            this.testDialogVisible = false;
          }else {
            this.uploadLoading = false;
            this.testDialogVisible = false;
            this.$message.error('预览推送失败!');
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },
    //审核操作
    passButton(type) {
      if (type) {
        let param = {
          tipsId: this.baseData.tipsId,
          versionCode: this.baseData.versionCode,
          approvalStatus: 1,
          // passCheckTest: this.isCeshi,//模拟测试人员
          // passCheckOperation: !this.isCeshi//模拟运营
        };
        this.$http
          .post("/scene/auditTips", param)
          .then(response => {
            if (response.data && response.data.code == "0") {
              this.$message({
                type: "success",
                message: "操作成功"
              });
              this.baseData.state = 6;
              this.isExam = true;
              this.$emit("tips-event", {
                type: "home-page"
              });
            } else {
              if (response.data.msg) {
                this.$message.error(response.data.msg);
              } else {
                this.$message.error("操作失败！");
              }
            }
          })
          .catch(() => {
            this.$message.error("操作失败！");
          });
      } else {
        this.examDialogVisible = true;
      }
    },
    //预览测试操作
    previewTest() {
      let url = location.origin + "/scene/getQrCode?url=" + this.testCardUrl;
      this.testImgSrc = url;
      this.testDialogVisible = true;
    },
    //审核弹窗
    examDialogClose(done) {
      if (typeof done === "function") {
        this.baseData.reason = "";
        done();
      } else if (done) {
        this.baseData.reason = "";
        this.examDialogVisible = false;
      } else {
        this.$refs["examData"].validate(valid => {
          if (valid) {
            let param = {
              tipsId: this.baseData.tipsId,
              versionCode: this.baseData.versionCode,
              approvalStatus: 2,
              approvalReason: this.baseData.reason,
              // passCheckTest: this.isCeshi,//模拟测试人员
              // passCheckOperation: !this.isCeshi//模拟运营
            };
            this.$http
              .post("/scene/auditTips", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.$message({
                    type: "success",
                    message: "操作成功"
                  });
                  this.baseData.state = 0;
                  this.isExam = true;
                  this.examDialogVisible = false;
                  this.$emit("tips-event", {
                    type: "home-page"
                  });
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("操作失败！");
                  }
                }
              })
              .catch(() => {
                this.$message.error("操作失败！");
              });
          } else {
            this.$message.warning("请按要求填写");
            return false;
          }
        });
      }
    },
    //回到列表页面
    goHome() {
      this.$emit("tips-event", {
        type: "home-page"
      });
    },
    //取消审核操作
    cancelEditButton() {
      this.$emit("tips-event", {
            type: "home-page"
          });
    },
    //获取数据xhr
    getDataXhr() {
      let param = {
        tipsId: this.baseData.tipsId,
        versionCode: this.baseData.versionCode
      };
      this.$http
        .post("/scene/getTipsDetail", param)
        .then(response => {
          if (
            response.data &&
            response.data.code == "0" &&
            response.data.data
          ) {
            let data = response.data.data
            this.nameData.name = data.name;
            this.nameData.versionCode = data.versionCode;
            this.contentData.time = [data.startTime,data.endTime];
            this.contentData.priority = data.priority;
            this.contentData.displayText = data.displayText;
            this.contentData.iconUrl = data.iconUrl;
            this.textLinkData = data.textLink;
            this.contentData.buttonName = data.buttonName;
            this.noticeData = data.notice?data.notice:{};
            this.noticeDialogVisible = data.notice?true:false;
            this.btnData = data.buttonName?data.buttonLink:{};
            this.btnDialogVisible = data.buttonLink ? true : false;
            this.approvalData.approvalTestUserName = data.approvalTestUserName;
            this.approvalData.approvalTestStatus = data.approvalTestStatus;
            this.approvalData.approvalTestReason = data.approvalTestReason;
            this.approvalData.approvalOperationUserName = data.approvalOperationUserName;
            this.approvalData.approvalOperationStatus = data.approvalOperationStatus;
            this.approvalData.approvalOperationReason = data.approvalOperationReason;
            this.imeiList = data.previewImeis?data.previewImeis.split(';'):[];
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },
  },
  //初始化请求页面数据
  mounted: function () {
    if (this.isCheckpage) {
      this.isEditPage = false;
      this.stepNumber = 2;
    }
    if (typeof this.baseData.tipsId !== 'undefined') {
      this.getDataXhr();
    }
  },
  created: function () {
    // this.getDataXhr();
  }
};
</script>

