<template>
  <div class="body-container">
    <div class="bread-crumb">
      <el-breadcrumb separator-class="el-icon-arrow-right bread-crumb">
        <el-breadcrumb-item :to="{ path: '/' }"><span @click="goHome">tips审核</span></el-breadcrumb-item>
        <el-breadcrumb-item v-if="isTipsShow">审核</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <header-component v-if="isTableShow" @header-event="headerEvent" :is-checkpage="isCheckPage" style="margin-top: 0px"></header-component>
    <table-component v-if="isTableShow" :data-list="dataList" @table-event="tableEvent" :is-checkpage="isCheckPage"></table-component>
    <footer-component v-if="isTableShow" :data-total="dataTotal" :current-page="pageIndex" :page-size="pageSize" @footer-event="footerEvent"></footer-component>
    <tips-Component v-if="isTipsShow" :is-add="isAdd" :base-data="baseData" @tips-event="tipsEvent" :is-detailpage="isDetailPage" :is-checkpage="isCheckPage"></tips-Component>
  </div>
</template>

<script>
import addComponent from "./components/AddComponent.vue"
import tableComponent from "./components/TableComponent.vue";
import headerComponent from "./components/HeaderComponent.vue";
import footerComponent from "./components/FooterComponent.vue";
import tipsComponent from "./components/TipsComponent.vue";

export default {
  name: "app",
  components: {
    addComponent,
    tableComponent,
    headerComponent,
    footerComponent,
    tipsComponent
  },
  data() {
    return {
      isTipsShow: false,
      isAdd: false,
      isDetailPage: false,
      isTableShow: true,
      isCheckPage: false,
      dataList: [
        
      ],
      pageIndex: 1,
      pageSize: 20,
      dataTotal: 0,
      searchName: "",
      searchModifyUser: "",
      searchType: "",
      searchState: "",
      searchField: "",
      searchOrder: "",

      baseData: {}
    };
  },
  methods: {
    goHome(){
      this.getDataXhr();
      this.isTipsShow = false;
      this.isAdd = false;
      this.isCheckPage = false;
      this.isDetailPage = false;
      this.isTableShow = true;
    },
    //活动管理
    tipsEvent(event) {
      switch (event.type) {
        case "home-page":
          this.goHome();
          break;
        case "data-page":
          //跳转大数据页面
          break;
        default:
          break;
      }
    },
    //表格事件
    tableEvent(event) {
      switch (event.type) {
        case "data-page":
          //跳转数据页面
          break;
        case "add-page":
          this.baseData = {};
          this.isTableShow = false;
          this.isAdd = true;
          this.isDetailPage = false;
          this.isCheckPage = false;
          this.isTipsShow = true;
          break;
        case "edit-page":
          this.baseData = event.data;
          this.isAdd = false;
          this.isTableShow = false;
          this.isDetailPage = false;
          this.isCheckPage = false;
          this.isTipsShow = true;
          break;
        case "detail-page":
          this.baseData = event.data;
          this.isAdd = false;
          this.isTableShow = false;
          this.isDetailPage = true;
          this.isCheckPage = false;
          this.isTipsShow = true;
          break;
        case "check-page":
          this.baseData = event.data;
          this.isAdd = false;
          this.isTableShow = false;
          this.isDetailPage = false;
          this.isCheckPage = true;
          this.isTipsShow = true;
          break;
        case "refresh-data":
          this.getDataXhr();
          break;
        case "sortChange":
          this.searchOrder = event.data.order;
          this.searchField = event.data.field;
          this.getDataXhr();
          break;
        default:
          break;
      }
          this.isCheckPage = true;

    },
    //页头事件
    headerEvent(event) {
      this.searchData(event.data);
    },
    //搜索请求
    searchData(data) {
      this.searchName = data.name;
      this.searchType = data.type;
      // if (this.isCheckPage) {
      //   this.searchState = 3;
      // } else {
        this.searchState = data.status;
      // }
      this.pageIndex = 1;
      this.getDataXhr();
    },
    //获取数据xhr
    getDataXhr() {
      var param = {
        name: this.searchName === "" ? undefined : this.searchName,
        field: this.searchField === "" ? undefined : this.searchField,
        order: this.searchOrder === "" ? undefined : this.searchOrder,
        page: this.pageIndex,
        size: this.pageSize
      };
      this.$http
        .post("/scene/getToAuditTipsList", param)
        .then(response => {
          this.dataTotal =
            response.data.data && response.data.data.totalElements;
          this.dataList = response.data.data && response.data.data.content;
        })
        .catch(() => {
          //console.log(error);
        });
    },
    //页脚事件
    footerEvent(event) {
      switch (event.type) {
        case "pageChange":
          this.pageIndex = event.data;
          this.getDataXhr();
          break;
        case "sizeChange":
          this.pageIndex = 1;
          this.pageSize = event.data;
          this.getDataXhr();
          break;
        default:
          break;
      }
    }
  },
  created: function () {
    if (this.isCheckPage) {
      this.searchState = 3;
    }
    this.getDataXhr();
  }
};
</script>

<style scoped>
.bread-crumb {
  padding: 30px 12px;
}
.body-container {
  min-height: 100%;
  padding-left: 70px;
  padding-right: 70px;
}
</style>
