<template>
  <div class="footer">
    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10,20,50,100,500,1000]" :page-size="pageSize" :pager-count="11" layout="total, sizes, prev, pager, next, jumper" :total="dataTotal">
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: "footer-component",
  props: {
    dataTotal: Number,
    currentPage: Number,
    pageSize:Number
  },
  methods: {
    handleCurrentChange(pageNumber) {
      this.$emit("footer-event", {
        type: "pageChange",
        data: pageNumber
      });
    },
    handleSizeChange(sizeNumber) {
      this.$emit("footer-event", {
        type: "sizeChange",
        data: sizeNumber
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.footer {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
</style>