/* eslint-disable */
<template>
  <div class="header-class">
    <div class="left-input">
      <el-input placeholder="名称" v-model="tipsName" clearable @change="searchData"></el-input>
    </div>
    <el-button @click="searchData" icon="el-icon-search" class="left-button" type="primary">搜索</el-button>
  </div>
</template>

<script>
export default {
  name: "header-component",
  props: {
    isCheckpage: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      tipsName: "",
    };
  },
  methods: {
    searchData() {
      this.$emit("header-event", {
        type: "search-event",
        data: {
          name: this.tipsName,
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header-class {
  width: 100%;
  height: 50px;
  margin-top: 30px;
}
.left-input {
  float: left;
  margin-left: 10px;
  width: 200px;
}
.left-button {
  float: left;
  margin-left: 20px;
}
.left-select {
  float: left;
  margin-left: 20px;
  width: 120px;
}
</style>